@import "sass/inputs";
@import "sass/modal";
@import "sass/header";

.markup-link {
    position: fixed;
    bottom: 15px;
    left: 15px;
    z-index: 2;
    background-color:$primary-color;
    padding: 5px;
    border-radius: 50%;
    transition: .2s;
    text-decoration: none;
    font-weight: 900;
    color: #fff;
    &:hover {
        background-color: $secondary-color;
        color: $text;
    }
}

